"use strict";

import "./index.less";
import React from "react";
import ReactDom from "react-dom";
import BaseUtil from "../base/util";

export default class Modal extends React.Component {
	static defaultProps = {
		id : "default-modal",
		title : "标题",
		content : "",
		actions : [{
			name : "确定",
			action : ($content) => {
				$content.remove();
			}
		}]
	};

	state = {
		isModalActive : true
	};

	constructor(props) {
		super(props);
	}

	render(){
	}

	onToolbarItemClick(event){
		this.setState({
			isModalActive : false
		});
		/**
		let itemIndex = event.target.getAttribute("data-index");
		let content = document.getElementById(this.props.id);
		let actionItem = this.props.actions[ itemIndex ];

		actionItem.action && actionItem.action(content);
		**/
	}

	closeModal(event){
		let className = event.target.getAttribute("class") || "";

		if(className.indexOf("m-modal") >= 0 || className.indexOf("close-icon") >= 0) {
			event.target.remove();
		}
	}

	active(){
		this.setState({
			isModalActive : true
		});
	}
}



